  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-size: 16px;
  background-color: #f4f4f4;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  color: #3d3d3d;
}



main,
header,
footer {
  margin: 0 auto;
  width: 100%;
}

header {
  padding: 12px 24px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  margin-bottom: 32px;
}

h1 {
  font-size: 32px;
  font-weight: bold;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 24px;
}

.scores-container {
  background-color: #f7f7f7;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  width: 300px;
}


.border {
  border: 1px solid #333;
}

#scores {
  border: 1px solid #ccc;
  border-radius: 5px;
}

#scores li {
  padding: 8px 8px;
    font-size: 18px;
}

#scores li:nth-child(even) {
  background-color: #dddddd91;
}

.d-width {
  min-width: 350px;
  margin: 12px;
}

.sub-title {
  color: #333333e7;
    font-size: 24px;
    font-weight: 600;
    
}

.input-field {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.sub-headline {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.form-container {
  background-color: #f7f7f7;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  width: 300px;
}

.l-container {
  display: flex;
  justify-content: center;
  align-items: first baseline;
  padding: 0 24px;
  gap: 24px;
}

form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
}
input {
  padding: 4px;
  font-size: 1rem;
}

.btn {
  align-self: flex-end;
  background-color: #6c757d;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 18px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.hidden {
  display: none;
}

#status {
  padding: 10px;
  background-color: #ffc107;
  color: #333;
  border-radius: 5px;
  font-size: 16px;
  margin-top: 10px;
  text-align: center;
}

#status.success {
  display: block;
  background-color: #28a745;
  color: #fff;
}

#status.error {
  display: block;
  background-color: #dc3545;
  color: #fff;
